@import url(https://cdn.jsdelivr.net/gh/orioncactus/pretendard@v1.3.9/dist/web/variable/pretendardvariable.min.css);
@import url(https://cdn.jsdelivr.net/gh/orioncactus/pretendard@v1.3.9/dist/web/variable/pretendardvariable.min.css);
html {
  scroll-behavior: smooth;
}

body {
  margin: 0;
  font-family: 'Pretendard', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

.section {
  padding: 2rem;
  border-bottom: 1px solid #ccc;
  min-height: calc(100vh - 80px); /* 네비게이션 바 높이만큼 빼서 화면 전체로 설정 */
}

.main-content {
  margin-top: 80px; /* 네비게이션 바 높이만큼 여백 추가 */
}

/* 기존 CSS 유지 */

.header {
  background-color: #000000;
  padding: 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: fixed;
  width: auto;
  height: 3em;
  top: 10px;
  left: 10px;
  right: 10px;
  z-index: 1000;
  border-radius: 50px;
  transition: background-color 0.3s, color 0.3s;
}

.header.dark {
  background-color: #ffffff;
  color: #000000;
}

.header.light {
  background-color: #000000;
  color: #ffffff;
}

.title-link, h1 {
  margin-left: 20px;
  text-align: left;
  color: inherit;
  font-size: 1.5rem;
  -webkit-text-decoration-line: none;
          text-decoration-line: none;
}

.nav-links {
  list-style: none;
  display: flex;
  margin: 0;
  padding: 0;
}

.nav-item {
  margin: 0 10px;
}

.nav-links a {
  color: inherit;
  text-decoration: none;
  font-size: 1.2rem;
}

@media screen and (max-width: 900px) {
  .nav-links {
    display: none; /* 모바일 화면에서는 기본 네비게이션 링크 숨김 */
  }

  .nav-toggle {
    display: block;
    cursor: pointer;
    font-size: 1.5rem;
    position: absolute; /* 헤더에 상대적으로 위치 */
    top: 50%; /* 헤더 중앙에 맞춤 */
    right: 20px; /* 오른쪽 여백 */
    transform: translateY(-50%); /* 세로 중앙 정렬 */
    z-index: 2001; /* 사이드바보다 위에 위치 */
  }
  
  /* 사이드바 스타일 */
  .sidebar {
    position: fixed;
    top: 0;
    right: 0;
    width: 250px;
    height: 100vh;
    background-color: #000000; /* 사이드바 배경색 */
    color: #ffffff; /* 사이드바 텍스트 색상 */
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    transform: translateX(100%);
    transition: transform 0.3s ease;
    z-index: 2000; /* 사이드바는 네비게이션 토글 버튼보다 낮은 z-index */
    padding: 20px;
  }

  .sidebar.open {
    transform: translateX(0);
  }

  .sidebar ul {
    list-style: none;
    padding: 0;
    margin: 0;
    text-align: center;
  }

  .sidebar li {
    margin: 10px 0;
  }

  .sidebar a {
    color: #ffffff; /* 사이드바 링크 텍스트 색상 */
    text-decoration: none;
    font-size: 1.5rem;
  }
}

@media screen and (min-width: 899px) {
  .nav-toggle {
    display: none;
  }

  .sidebar {
    display: none;
  }
}

.nav-links a:hover, .sidebar a:hover {
  color: rgb(156, 255, 141);
  text-decoration: none;
  font-weight: bold;
}

/* src/sections/About.css */

/* About section 기본 스타일 */
#about {
    background-color: #ffffff; /* 배경색 */
    color: black; /* 글자 색상 */
    padding: 50px;
    margin: 0 auto; /* 중앙 정렬을 위해 자동 마진 */
    max-width: 100%; /* 최대 너비 설정 */
    box-sizing: border-box; /* padding을 포함하여 전체 너비를 계산 */
  }
  
  /* 로고 스타일 */
  .logo {
    width: 90%; /* 화면 너비에 맞게 로고 크기 조정 */
    height: auto; /* 비율 유지 */
    display: block; /* 블록 요소로 설정하여 줄 바꿈을 강제 */
  }
  
  /* About 콘텐츠 스타일 */
  .about-content {
    display: flex;
    flex-direction: row; /* 기본적으로 세로 정렬 */
    justify-content: center;
    text-align: justify; /* 텍스트 정렬을 왼쪽으로 */
    width: 100%; /* 전체 너비 설정 */
    grid-gap: 20px;
    gap: 20px; /* 좌우 간격 */
  }
  
  /* 왼쪽과 오른쪽 콘텐츠 스타일 */
  .about-left-content {
    width: 60%; /* 왼쪽 컨텐츠는 60% */
    border-right: 1px solid black; /* 왼쪽과 오른쪽을 나누는 경계선 */
    padding-right: 20px; /* 경계선과 텍스트 간의 간격 */
  }
  
  .about-right-content {
    width: 40%; /* 오른쪽 컨텐츠는 40% */
    padding-left: 20px; /* 경계선과 텍스트 간의 간격 */
  }
  
  .about-left-content, .about-right-content {
    max-width: 1000px;
    margin: 0;
    padding: 10px;
  }
  
  /* 기본 텍스트 스타일 */
  .about-left-content p, .about-right-content p {
    font-size: 1.2em; /* 기본 텍스트 크기 */
    margin: 20px 0; /* 텍스트 간의 간격 설정 */
    line-height: 1.5;
  }
  
  /* 미디어 쿼리: 화면 크기에 따른 조정 */
  @media screen and (max-width: 1024px) {
    .about-left-content p, .about-right-content p {
      font-size: 1em; /* 화면 너비가 1024px 이하일 때 글자 크기 조정 */
    }
  }
  
  @media screen and (max-width: 768px) {
    .about-content {
      flex-direction: column; /* 작은 화면에서는 세로 방향으로 배치 */
    }
  
    .about-left-content {
      border-right: none; /* 세로 구분선 제거 */
      border-bottom: 1px solid black; /* 가로 구분선 추가 */
      padding-right: 0; /* 오른쪽 패딩 제거 */
      padding-bottom: 20px; /* 아래쪽 패딩 추가 */
      width: 100%; /* 전체 너비를 활용 */
      box-sizing: border-box; /* padding을 포함하여 전체 너비를 계산 */
    }
  
    .about-right-content {
      padding-left: 0; /* 왼쪽 패딩 제거 */
      width: 100%; /* 전체 너비를 활용 */
      box-sizing: border-box; /* padding을 포함하여 전체 너비를 계산 */
    }
  
    .about-left-content p, .about-right-content p {
      font-size: 1em; /* 화면 너비가 768px 이하일 때 글자 크기 조정 */
    }
  }
  
  @media screen and (max-width: 480px) {
    .about-left-content p, .about-right-content p {
      font-size: 0.8em; /* 화면 너비가 480px 이하일 때 글자 크기 조정 */
    }
  }
  
/* src/pages/project.css */

/* 부모 컨테이너 설정 */
.section {
  display: flex;
  flex-direction: column;
  align-items: center; /* 수직 중앙 정렬 (필요 시) */
}

/* 프로젝트 그리드 레이아웃 */
.projects-grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr); /* 한 행에 4개의 열로 설정 */
  grid-gap: 30px;
  gap: 30px; /* 사각형 사이의 간격 설정 */
  width: 100%; /* 그리드의 너비를 부모 컨테이너에 맞춤 */
  max-width: 1400px; /* 최대 너비 설정 */
  margin: 0 auto; /* 수평 중앙 정렬 */
  padding-top: 40px;
  padding-bottom: 30px;
}

.project-preview {
  position: relative;
  overflow: hidden;
  background-color: #ccc; /* 회색 배경 */
  border-radius: 12px; /* 이미지 모서리 둥글게 설정 */
  transition: transform 0.3s ease;
  width: 90%;
  height: 0%;
  padding-bottom: 56.25%; /* 16:9 비율로 설정 */
  margin: auto; /* 이미지의 수평 및 수직 중앙 정렬 */
}

.project-preview:hover {
  transform: scale(1.05); /* 마우스 호버 시 사각형 크기 확대 */
}

.project-preview-inner {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
}

/* 이미지 설정 */
.project-preview-inner img {
  position: absolute;
  width: 100%;
  height: 100%;
  object-fit: cover; /* 이미지를 비율유지하면서 영역을 가득 채우도록 설정 */
}

/* 오버레이 설정 */
.project-preview-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5); /* 반투명 배경 */
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  opacity: 0; /* 초기에는 보이지 않음 */
  transition: opacity 0.3s ease;
}

.project-preview:hover .project-preview-overlay {
  opacity: 1; /* 호버 시 투명도 조정하여 텍스트 표시 */
}

/* 반응형 디자인 */
@media (max-width: 1024px) {
  .projects-grid {
    grid-template-columns: repeat(3, 1fr); /* 화면 크기가 줄어들 때 3열로 변경 */
  }
}

@media (max-width: 768px) {
  .projects-grid {
    grid-template-columns: repeat(1, 1fr); /* 화면 크기가 더 줄어들 때 1열로 변경 */
  }
}

/* 교수님 섹션 */
.professor-container {
