/* src/styles/Pics.css */

.photo-gallery {
  display: flex;
  flex-wrap: wrap;
  /* 화면 크기에 따라 줄 바꿈 허용 */
  justify-content: center;
  /* 가운데 정렬 */
  grid-gap: 10px;
  gap: 10px;
  /* 사진들 사이의 간격 설정 */
  margin-bottom: 20px;
  /* 페이지네이션과의 간격 설정 */
}

.photo-gallery .photo_pics {
  flex-grow: 1;
  flex-shrink: 1;
  flex-basis: calc(25% - 20px);
  /* 사진을 4등분으로 나누고, 간격을 고려하여 크기 설정 */
  max-width: calc(25% - 20px);
  /* 최대 너비를 25%로 설정 */
  height: auto;
  /* 높이는 자동 조절 */
  object-fit: cover;
  /* 이미지 비율 유지 */
}

/* 화면 크기가 작은 경우 사진의 너비를 50%로 조정 */
@media (max-width: 768px) {
  .photo-gallery .photo_pics {
    flex-grow: 1;
    flex-shrink: 1;
    flex-basis: calc(50% - 20px);
    /* 사진을 2등분으로 나누고, 간격을 고려하여 크기 설정 */
    max-width: calc(50% - 20px);
    /* 최대 너비를 50%로 설정 */
  }
}

/* 페이지네이션 */
.pagination {
  display: flex;
  justify-content: center;
  /* 가운데 정렬 */
  margin-top: 20px;
  /* 위쪽 간격 설정 */
}

.pagination-button {
  background: none;
  border: none;
  font-size: 24px;
  cursor: pointer;
  margin: 0 5px;
  /* 버튼 간격 설정 */
}

.pagination-button.active {
  color: black;
  /* 활성화된 버튼 색상 */
}

.pagination-button:not(.active) {
  color: grey;
  /* 비활성화된 버튼 색상 */
}

.pagination-button:focus {
  outline: none;
}
/* src/pages/designers.css */

/* 디자이너 콘텐츠 */
.designers-content {
  display: flex;
  flex-wrap: wrap;
  /* 화면 크기에 따라 줄 바꿈 허용 */
  grid-gap: 20px;
  gap: 20px;
  /* 왼쪽과 오른쪽 사이의 간격 */
  align-items: stretch;
  /* 자식 요소의 높이 동일하게 설정 */
}

.designers-text-content {
  flex: 3 1;
  /* 텍스트 컨텐츠는 3 비율 */
  padding: 20px;
}

.designers-image-content {
  flex: 2 1;
  /* 이미지 컨텐츠는 2 비율 */
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px;
  overflow: hidden;
  /* 이미지가 부모 요소를 넘어서지 않도록 설정 */
}

.designers-image-content img {
  max-width: 90%;
  /* 이미지 최대 너비를 부모 요소의 90%로 설정 */
  max-height: 90%;
  /* 이미지 최대 높이를 부모 요소의 90%로 설정 */
  width: auto;
  /* 너비 자동 조절 */
  height: auto;
  /* 높이 자동 조절 */
  object-fit: contain;
  /* 이미지 비율 유지하며 부모 요소 안에 맞추기 */
}

.designers-text-content h2,
.designers-text-content p {
  font-size: 1.3em;
  /* 텍스트 크기를 기본 크기의 1.5배로 설정 */
  margin: 10px 0;
  /* 텍스트 간의 간격 설정 */
  line-height: 2;
}

.greenName {
  color: rgb(156, 255, 141);
}

/* 반응형 디자인 */
@media (max-width: 899px) {
  .designers-content {
    flex-direction: column;
    /* 텍스트와 이미지를 세로로 배치 */
  }

  .designers-text-content h2,
  .designers-text-content p {
    font-size: 1.15em;
    /* 텍스트 크기를 1.2em으로 줄임 */
  }
}
